.statistics {
  text-align: center;
}

.title {
  margin: 0;
  margin-top: 50px;
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 30px;
  line-height: 1.1;
}

.statistics_data {
  display: flex;
  text-align: center;
  justify-content: center;
}

.text {
  margin: 0;
  padding: 5px;
  min-width: 100px;
  border-radius: 3px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.3;
  background: rgb(190, 226, 241);
}

.text:not(:last-child) {
  margin-right: 30px;
}

.analitics {
  margin-top: 15px;
}

.analitics_text {
  margin: 0;
  font-weight: 700;
  font-size: 20px;
}
